<template>
  <tabbar class="foot-bar">
    <tabbar-item :is-on="focus==='1'?true:false" to="/">
       ♖♜首页
    </tabbar-item>
    <tabbar-item :is-on="focus==='2'?true:false" to="/cartoon/index.html">
        ♕♛漫画
    </tabbar-item>
    <tabbar-item :is-on="focus==='3'?true:false" to="/hobby/index.html">
        ♙♟周边
    </tabbar-item>
    <tabbar-item  :is-on="focus==='4'?true:false" to="/user/index.html">
        ♘♞我的
    </tabbar-item>
  </tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'we-vue'

//Vue.use(Tabbar).use(TabbarItem)

export default {
    name: 'footbar',
    components: {
        Tabbar,
        TabbarItem
    },
    props: {
        focus: {
            type: String,
            default: '1'
        }
    }
}
</script>

<style lang="less">
@import "~style/variables.less";
.foot-bar{
    .weui-bar__item_on{
        background-color: #f60;   
        .weui-tabbar__label{
            color: #fff; 
        } 
    }
}

</style>



